export default {
	props: {
		movies: {
			type: Array,
			value: [],
		},
		defaultSelectID: {
			type: String,
			default: ''
		}
	},
	data() {
		return {
			movies: [],
			movie: null, //选中的电影
			scrollLeft: 0, //设置滚动条位置
			size: 0, //电影item的大小（包括margin）
			i: 0 //当前电影的索引,下面的函数传递了事件对象后就不能传递其他参数了，所以只能放在data中传递
		}
	},
	computed: {
		bgImg() {
			return `background-image: url(${this.movie.img})`
		}
	},
	methods: {
		selectMovie(curMovie) {
			const {
				movies
			} = this
			const movie = curMovie || movies.find(item => item.id == this.defaultSelectID) ||movies[this.i]
			if (movies.length && this.movie && movie.id === this.movie.id) {
				return
			}
			const index = movies.findIndex(item => item.id === movie.id)
			if (this.size) {
				this.movie = movie
				this.scrollLeft = this.size * index
			} else {
				this.calcSize().then((size) => {
					this.movie = movie
					this.size = size
					this.scrollLeft = size * index
				})
			}
			this.$emit('selectMovie', {
				movie
			})
		},
		// 计算节点大小
		calcSize() {
			return new Promise((resolve, reject) => {
				const query = uni.createSelectorQuery().in(this)
				query.select(`#item1`).fields({
					size: true,
					computedStyle: ['margin-left']
				}, function(res) {
					let size = 0
					if (res) {
						size = res.width + parseFloat(res['margin-left'])
					}
					resolve(size)
				}).exec()
			})
		}
	}
}
